:export {
  blue: #298df8;
}
.glass {
  position: relative;
}
.glass::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.clearfix::before,
.clearfix::after {
  content: " ";
  display: inline-block;
  height: 100%;
  visibility: hidden;
  clear: both;
}
.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.el-scrollbar {
  position: relative;
  overflow: hidden;
}
.el-scrollbar.fitHeight {
  height: 100%;
}
.el-scrollbar__bar {
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  opacity: 0;
  -webkit-transition: opacity 120ms ease-out;
  transition: opacity 120ms ease-out;
  border-radius: 4px;
}
.el-scrollbar:active > .el-scrollbar__bar,
.el-scrollbar:focus > .el-scrollbar__bar,
.el-scrollbar:hover > .el-scrollbar__bar {
  opacity: 1;
  transition: opacity 340ms ease-out;
}
.el-scrollbar__wrap {
  overflow: scroll;
}
.el-scrollbar__wrap--hidden-default {
  scrollbar-width: none;
}
.el-scrollbar__wrap--hidden-default::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.el-scrollbar__view {
  display: inline-block;
}
.el-scrollbar__thumb {
  position: relative;
  display: block;
  width: 0;
  height: 0;
  cursor: pointer;
  background-color: #454647;
  transition: 0.3s background-color;
  border-radius: inherit;
}
.el-scrollbar__thumb:hover {
  background-color: #595a5c;
}
.el-scrollbar__bar.is-vertical {
  top: 2px;
  width: 6px;
}
.el-scrollbar__bar.is-vertical > div {
  width: 100%;
}
.el-scrollbar__bar.is-horizontal {
  left: 2px;
  height: 6px;
}
.el-scrollbar__bar.is-horizontal > div {
  height: 100%;
}
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  color: #000;
  user-select: none;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
.CodeMirror-lint-tooltip {
  z-index: 10000;
}
